.land-work_showcase {
	background-color: #FFFFFF;
}

.land-showcase_top {
	padding: 80px 0 16px 0;
}

.showcase_title {
	padding-bottom: 16px;
}

.land-showcase_filter {
	display: flex;
	flex-wrap: wrap; // щоб кнопки переносились на новий рядок на маленьких екранах
	margin-top: 10px;
}

.land-filter-btn {
	padding: 10px 16px;
	margin-right: 6px;
	margin-bottom: 10px;
	margin-top: 0;

	font-family: 'Montserrat', sans-serif;
	font-size: 14px;
	font-style: normal;
	font-weight: 700;
	line-height: 100%;
	border: none;
	border-radius: 9999px;
	background-color: transparent;
	color: #000;
	cursor: pointer;
	transition: all 0.4s ease-in-out;

	&.active {
		background-color: #000;
		color: #fff;
		border-color: #000;
	}

	&:hover {
		background-color: #000;
		color: #fff;
	}

	&:last-child {
		margin-right: 0;
	}
}



.land-showcase_slide {
	display: flex;
	max-width: 891px !important;
	height: 600px !important;
	padding: 24px;
	flex-direction: column;
	align-items: flex-start;
	gap: 24px;


	border-radius: 24px;
	background: var(--Body-Light-Grey, #F9F9F9);

	.land-showcase_slide__title {
		display: grid;
		gap: 4px;
		padding-bottom: 26px;

		.land-title_top {
			color: var(--Text-Secondary, #434343);

			/* Body/med 16px */
			font-family: Montserrat;
			font-size: 16px;
			font-style: normal;
			font-weight: 500;
			line-height: 140%; /* 22.4px */
		}

		.land-title_bottom {
			color: var(--Text-Primary, #262626);

			/* Heading/h4 */
			font-family: Unbounded;
			font-size: 22px;
			font-style: normal;
			font-weight: 500;
			line-height: 110%; /* 24.2px */
		}
	}


	.land-showcase_slide_img {
		display: flex;
		border-radius: 16px;
		width: 100%;
		overflow: hidden; /* важливо */
		max-height: 478px;
		height: 100%;
	}
}

.land-showcase_swiper {
	position: relative !important;
	margin-left: calc(50% - 50vw) !important; // зсунути, щоб вирівняти з екраном
	padding-left: calc((100vw - 1200px) / 2) !important; // старт зліва як у .container
	overflow: hidden !important;
	padding-bottom: 118px !important;

	.swiper-wrapper {
		display: flex;
	}


	.land-swiper-slide {
		flex-shrink: 0;
	}

	.swiper-pagination {
		position: absolute;
		bottom: 80px!important ;
		left: 0!important;
		width: 100% !important;
		display: flex;
		justify-content: center;
		gap: 12px;

		.swiper-pagination-bullet {
			width: 6px;
			height: 6px;
			border-radius: 50%;
			background: #959595;
			opacity: 0.4;
			position: relative;
			z-index: 1;
		}

		// Активний індикатор
		&::after {
			content: '';
			position: absolute;
			top: 50%;
			height: 6px;

			border-radius: 6px;
			background: var(--Text-Secondary, #434343);
			transform: translateY(-50%);
			transition: all 0.15s ease-in-out;
			z-index: 2;

			/* динамічні змінні */
			left: var(--bullet-left, 0);
			width: var(--bullet-width, 6px);
		}
	}


}

.land-showcase_swiper__pre {
	position: relative;
}

.land-preloader {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(255, 255, 255, 0.9);
	backdrop-filter: blur(2px);
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.3s ease, visibility 0.3s ease;
	z-index: 10;

	display: flex;
	justify-content: center;
	align-items: center;

	.land-preloader__svg {
		width: 100px;
		height: 100px;
	}
}

.land-preloader img {
	width: 60px; /* розмір гіфки */
	height: 60px;
	object-fit: contain;
}

.land-preloader.active {
	opacity: 1;
	visibility: visible;
}

.land-showcase_bottom {
	background: var(--Body-Light-Grey, #F9F9F9);

	.land-showcase_bottom__title {
		padding: 59px 0 32px 0;
		color: var(--Text-Primary, #262626);

		font-family: 'Unbounded', sans-serif;
		font-size: 22px;
		font-style: normal;
		font-weight: 500;
		line-height: 110%; /* 19.8px */
	}

	.land-showcase_slide_bottom {
		height: auto;
		max-width: 120px;
	}


	.land-showcase_swiper_bottom {
		padding-bottom: 59px;
		margin-left: calc(50% - 50vw);
		padding-left: calc((100vw - 1200px) / 2);
		overflow: hidden;

		.swiper-slide {
			flex-shrink: 0;
			width: auto;
		}
	}

}


@media (max-width: 1199px) {
	.land-showcase_bottom .land-showcase_swiper_bottom {
		padding-bottom: 48px;
	}
	.land-showcase_bottom .land-showcase_bottom__title{
		padding-top: 48px;
	}
	.land-showcase_top {
		padding: 48px 0 16px 0;
	}
	.land-showcase_filter {
		display: flex;
		flex-wrap: nowrap;
		overflow-x: auto;
		scroll-snap-type: x mandatory; /* вмикаємо слайдер-прив’язку */
		-webkit-overflow-scrolling: touch; /* плавність на iOS */
		gap: 10px;
		padding-bottom: 5px;
	}

	/* Приховуємо скролбар у Chrome/Safari */
	.land-showcase_filter::-webkit-scrollbar {
		height: 6px;
	}
	.land-showcase_filter::-webkit-scrollbar-track {
		background: transparent;
	}
	.land-showcase_filter::-webkit-scrollbar-thumb {
		background: #01D0CC;
		border-radius: 10px;
	}

	.land-filter-btn {
		flex: 0 0 auto; /* кнопка не стискається */
		scroll-snap-align: start; /* прилипає при скролі */

		padding: 10px 15px;
		font-family: 'Montserrat', sans-serif;
		font-size: 14px;
		font-style: normal;
		font-weight: 700;
		line-height: 100%;
		border: none;
		border-radius: 9999px; /* повністю округлі краї */
		background-color: transparent;
		color: #000;
		cursor: pointer;
		transition: all 0.4s ease-in-out;
	}

	.land-filter-btn.active {
		background-color: #000;
		color: #fff;
		border-color: #000;
	}
}

