@media (max-width: 768px) {
	.land-showcase_top {
		padding: 0 0 16px 0;
	}
	.land-showcase_bottom .land-showcase_swiper_bottom {
		padding-bottom: 40px;
	}
	.land-showcase_bottom .land-showcase_bottom__title {
		padding: 40px 0 24px 0;
	}
	.land-showcase_swiper .swiper-wrapper {
		gap: 0;
	}
	.land-showcase_slide {
		height: 380px!important ;
	}
	.land-showcase_slide_img {
		max-height: 250px;
	}
	.land-showcase_slide .land-showcase_slide_img {
		max-height: 77%;
	}
}

@media (max-width: 480px) {
	.swiper-button-prev, .swiper-rtl .swiper-button-next {
		left: var(--swiper-navigation-sides-offset,0px)!important;
	}
	.swiper-button-next, .swiper-rtl .swiper-button-prev {
		right: var(--swiper-navigation-sides-offset,0px)!important;
	}

	.swiper-button-prev::after {
		color: #838382;
	}
	.swiper-button-next::after{
		color: #838382;
	}
	.land-showcase_slide .land-showcase_slide__title{
		padding-bottom: 10px;
	}
	.land-showcase_slide .land-showcase_slide__title .land-title_bottom{
		font-size: 16px;
	}
	.land-showcase_slide .land-showcase_slide__title .land-title_top{
		font-size: 14px;
	}
	.land-showcase_slide{
		padding: 10px;
	}
	.land-showcase_slide .land-showcase_slide_img {
		max-height: 77%;
	}
	.land-showcase_slide {
		height: 243px !important;
		max-width: 90%!important;
	}
	.land-showcase_swiper .swiper-pagination {
		bottom: 20px !important;
	}
	.land-showcase_swiper {
		padding-bottom: 55px !important;
	}
	.land-showcase_top {
		padding: 0 0 16px 0;
	}
	.land-showcase_title {
		padding-bottom: 24px;

	}
	.icv__circle{
		height: 40px!important;
		width: 40px!important;
	}
}


body.icv__body {
	position: static !important;
	left: auto !important;
}

.icv__theme-wrapper{
	cursor: pointer!important;
}






